import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

const LS = {
  load(){
    return JSON.parse(localStorage.getItem('vue-todo') || '[]')
  },
  save(data){
    localStorage.setItem('vue-todo',JSON.stringify(data))
  }
}
const filter = {
  all(todos){
    return todos
  },
  active(todos){
    return todos.filter((todo=>{
      return !todo.complete
    }))
  },
  complete(todos){
    return todos.filter((todo=>{
      return todo.complete
    }))
  }
}

export default new Vuex.Store({
  // strict: true,
  state: {
    todos: []
  },
  getters: {
    TODOS(state){
    let newArr = state.todos
    newArr.sort(function(a,b){
      let scorea = (a.complete?10000:0)
      let scoreb = (b.complete?10000:0)
      if(scorea < scoreb) return -1
      if(scorea > scoreb) return 1
      return 0
    })
      return filter[state.route.name](newArr).map(el=>{
        return newArr.indexOf(el)
      })
    }
  },
  mutations: {
    SET_TODOS(state,data){
      state.todos = data
      LS.save(state.todos)
    },
    ADD_TODO(state,data){
      state.todos.push(data)
      LS.save(state.todos)
    },
    DELETE_TODO(state,index){
      state.todos.splice(index,1)
      LS.save(state.todos)
    },
    UPDATE_TODO(state,{index,data}){
      state.todos[index].complete = data.complete
      state.todos[index].content = data.content
      state.todos[index].date = data.date
      LS.save(state.todos)
    }
  },
  actions: {
    INIT_TODOS({commit}){
      commit('SET_TODOS',LS.load())
      // commit('SORT_TODO')
    },
  }
})

