<script>
import { mapMutations } from 'vuex'
export default {
  data(){
    return{
      date: '',
      todo: '',
    }
  },
  methods: {
    ...mapMutations(['ADD_TODO']),
    submitHandler(){
      if(!this.todo || !this.date) return
      this.ADD_TODO({
        content: this.todo,
        date: this.date,
        complete: false
      });
      this.todo = '';
      this.date = '';
    },
    cancelHandler(){
      this.todo = '';
      this.date = '';
    }
  }
}
</script>
<template lang="pug" src="./template.pug"> </template>
<style lang="sass" src="./style.sass"></style>

