<script>
import { mapMutations, mapGetters } from 'vuex'
export default {
  data(){
    return{
      editText: null,
      editDate: null,
      check: false
    }
  },
  props: ['index']
  ,
  computed: {
    todo(){
      return this.$store.state.todos[this.index]
    },
    complete: {
      get(){
        return this.todo.complete
      },
      set(){
        this.UPDATE_TODO({
          index: this.index,
          data: {
            content: this.todo.content,
            complete: !this.todo.complete,
            date: this.todo.date
          }
        }) 
      }
    }
  },
  methods: {
    ...mapGetters(['NEWTODO']),
    ...mapMutations(['DELETE_TODO','UPDATE_TODO','COMPLETE']),
    deleteHandler(){
      if(confirm(`確定刪除「 ${this.todo.content}」？`))
      this.DELETE_TODO(this.index)
    },
    editHandler(){
      this.editText = this.todo.content
      this.editDate = this.todo.date
    },
    cancelHandler(){
      this.editText = null
      this.editDate = null
    },
    submitHandler(){
      if(!this.editText) {
        this.deleteHandler()
        this.editText = this.todo.content
        this.editDate = this.todo.date
      }
      this.UPDATE_TODO({
        index: this.index,
        data: {
          content: this.editText,
          complete: this.todo.complete,
          date: this.editDate
        }
      })
      this.cancelHandler()
    }
  }
}
</script>

